.App-logo {   
  margin-bottom: 10x;
}

h1 {
  font-family: "Courier New";
  color:  #026917;
  margin: 0;
  margin-top: 20px;
  padding: 0;
  float: left;

}

.App-Header {
  float: right;
  padding: 20px 0;
}


.container {
	width: 80%;
	margin: 0 auto;
}

header {
  background: #f5f5f5;
  padding-bottom: 20px;
}

header::after {
  content: '';
  display: table;
  clear: both;
}

nav {
  float: right;
  height: 20%;
}

nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

nav li {
  display: inline-block;
  margin-left: 70px;
  padding-top: 10px;

  position: relative;
}

nav a:hover {
  color: #000;
}

nav a::before {
  content: '';
  display: block;
  height: 5px;
  background-color: #026917;

  position: absolute;
  top: 0;
  width: 0%;

  transition: all ease-in-out 250ms;
}

nav a:hover::before {
  width: 100%;
}


.buttons {
  color: #026917;
  font-family: "Courier New";
}

Link {
  color: #026917;
}



@media (prefers-reduced-motion: no-preference) {
  #App-logo-two {
    animation: App-logo-alternate infinite alternate 1s ;
  }
}

@keyframes App-logo-alternate {
  0% {
    opacity: 0;
  }
  49% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}
